.social__list {
  width: 132px;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}

.social__item {
  width: 28px;

  &:hover .social__icon {
    fill: $basicGreen;
  }

  &:active .social__icon {
    opacity: 0.3;
  }
}

.social__icon {
  display: block;
  margin: auto;
}
